import axios from 'axios'
const base_url = 'http://localhost:3001/api'

const initialState = {
  user: {},
  loggedIn: false,
  loading: false
}

const LOGIN_USER = 'LOGIN_USER'

function userReducer (state = initialState, action) {
  switch (action.type) {
    case `${LOGIN_USER}_PENDING`:
      return { ...state, loading: true }
    case `${LOGIN_USER}_FULFILLED`:
      // window.location.pathname = '/';
      const { 
        username,
        email,
        password,
        authTokenOne,
        authTokenTwo,
        userIdentifier,
        emailVerified,
      } = action.payload.data[0];

      localStorage.setItem('user', {
        username: JSON.stringify(username),
        email: JSON.stringify(email),
        password: JSON.stringify(password),
        authTokenOne: JSON.stringify(authTokenOne),
        authTokenTwo: JSON.stringify(authTokenTwo),
        userIdentifier: JSON.stringify(userIdentifier),
        emailVerified: JSON.stringify(emailVerified),
      })

      return {
        ...state,
        user: action.payload.data[0],
        loading: false,
        loggedIn: true
      }
    default:
      return { ...state }
  }
}

export const loginUser = (username, password) => {
  return {
    type: LOGIN_USER,
    payload: axios.post(base_url + '/login_user', { username, password })
  }
}

export default userReducer
